import React, { Component, Fragment } from "react";
import { Row, Container, CardBody } from "reactstrap";
import { Colxx } from "../../../components/common/CustomBootstrap";
import { connect } from "react-redux";
import PatientConditions from "./patient-conditions";
import PatientList from "./patient-list";
import {
  getPatientsList,
  getInvitationsList,
  updateInvitation,
  logoutUser
} from "../../../redux/actions";
import * as chartData from "../../../helpers/chartDataMocks";

class PatientsListHolder extends Component {
  constructor(props) {
    super(props);

    this.state = {
      charts: null
    };
  }

  componentDidMount() {
    this.props.getPatientsList();
    this.props.getInvitationsList();

    setTimeout(() => {
      this.setState({ charts: { ...chartData } });
    }, 1000);

    // this.props.updateInvitation({
    //   relationId,
    //   data
    // });
  }

  getPatientData = data => {
    this.setState({ charts: null });

    setTimeout(() => {
      this.setState({ charts: { ...chartData } });
    }, 500);
  };

  processInvitationRequest = (id, accept) => {
    console.log(id, accept);
  };

  render() {
    return (
      <Fragment>
        <Row>
          <Colxx
            xxs="3"
            xs="3"
            sm="3"
            md="3"
            lg="3"
            xl="3"
            xxl="3"
            className="card mb-4"
          >
            <CardBody>
              <Container fluid={true}>
                <PatientList
                  header={"menu.invitations-list"}
                  invitations={this.props.invitations || { results: [] }}
                  onAcceptInvitation={this.processInvitationRequest}
                  onDeclineInvitation={this.processInvitationRequest}
                />
                <PatientList
                  header={"menu.patients-list"}
                  patients={this.props.patients || { results: [] }}
                  onGetPatientData={this.getPatientData}
                />
              </Container>
            </CardBody>
          </Colxx>
          <Colxx xxs="9" xs="9" sm="9" md="9" lg="9" xl="9" xxl="9">
            <Container fluid={true}>
              {this.state.charts && (
                <PatientConditions {...this.state.charts} />
              )}
            </Container>
          </Colxx>
        </Row>
      </Fragment>
    );
  }
}

const mapStateToProps = ({ authUser, patientsList }) => {
  return {
    ...authUser,
    ...patientsList
  };
};

export default connect(mapStateToProps, {
  getPatientsList,
  getInvitationsList,
  updateInvitation,
  logoutUser
})(PatientsListHolder);
