// data mocks
import { screenTimeData } from "../raw-data/screen_time_data";
import { sleepData } from "../raw-data/sleep_data";
import { yearFitbitChargerData } from "../raw-data/year_fitbit_chargehr_data";
// Theme color support
import { ThemeColors } from "./ThemeColors";
const colors = ThemeColors();

const sleepDataMock = sleepData
  .sort((a, b) => {
    return new Date(a["Start"]).getTime() - new Date(b["Start"]).getTime();
  })
  .slice(0, 365)
  .map(obj => {
    obj["Chart_date"] = obj["End"].replace(/\d{4}\s.+$/, "2019");

    return obj;
  })
  .sort((a, b) => {
    return (
      new Date(a["Chart_date"]).getTime() - new Date(b["Chart_date"]).getTime()
    );
  });
export const lineChartSleepData = {
  labels: sleepDataMock.map(obj => obj["Chart_date"]),
  datasets: [
    {
      label: "Time in bed",
      data: sleepDataMock.map(obj =>
        new Date(
          new Date(obj["End"]).getTime() - new Date(obj["Start"]).getTime()
        ).getHours()
      ),
      backgroundColor: colors.themeColor1_10,
      borderColor: colors.themeColor1,
      pointBackgroundColor: colors.foregroundColor,
      pointBorderColor: colors.themeColor1,
      pointHoverBackgroundColor: colors.themeColor1,
      pointHoverBorderColor: colors.foregroundColor,
      pointBorderWidth: 1,
      pointHoverRadius: 5,
      pointHoverBorderWidth: 2,
      pointRadius: 1,
      pointHitRadius: 10,
      fill: true
    }
  ]
};

const screenTimeDataMock = screenTimeData
  .map(obj => {
    obj["Date"] = obj["Date"].replace(/\d{4}$/, "2019");
    return obj;
  })
  .sort((a, b) => {
    return new Date(a["Date"]).getTime() - new Date(b["Date"]).getTime();
  });
export const lineChartScreenTimeData = {
  labels: screenTimeDataMock.map(obj => obj["Date"]),
  datasets: [
    {
      label: "Total screen time",
      data: screenTimeDataMock.map(obj =>
        parseInt(obj["Total_Screen_Time"], 10)
      ),
      backgroundColor: colors.themeColor1_10,
      borderColor: colors.themeColor1,
      pointBackgroundColor: colors.foregroundColor,
      pointBorderColor: colors.themeColor1,
      pointHoverBackgroundColor: colors.themeColor1,
      pointHoverBorderColor: colors.foregroundColor,
      pointBorderWidth: 1,
      pointHoverRadius: 5,
      pointHoverBorderWidth: 2,
      pointRadius: 1,
      pointHitRadius: 10,
      fill: true
    }
  ]
};

